<template>
  <div class="main">
    <router-view class="view"></router-view>
    <van-tabbar v-model="active">
      <van-tabbar-item icon="wap-home" to="/">首页</van-tabbar-item>
      <van-tabbar-item icon="contact" to="/contact">联系我们</van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
import {Tabbar, TabbarItem} from 'vant'
export default {
  components: {
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem,
  },
  data() {
    return {
      active: 0,
    }
  },
}
</script>

<style>
.main {
  height: 100%;
}
.main .van-tabbar-item--active {
  color: inherit;
}
.view {
  height: calc(100% - 50px);
  overflow-y: auto;
}
</style>



